/* Navigation.css */

nav {
    background-color: #444;
    padding: 10px;
  }
  
  ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
  li {
    margin: 0 15px;
  }
  
  a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    font-size: 1.1em;
    transition: color 0.3s ease;
  }
  
  a:hover {
    color: #ffd700; /* Change color on hover (you can customize this) */
  }
  