/* Contact.css */

.contact {
    background-color: #f9f9f9;
    padding: 50px;
    text-align: center;
  }
  
  .contact h2 {
    color: #333;
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .contact p {
    color: #555;
    font-size: 1.2em;
    margin-bottom: 40px;
  }
  
  form {
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  label {
    margin: 15px 0;
    color: #333;
    font-weight: bold;
    float: left;
    font-size: 1.2em;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 15px;
    margin-bottom: 25px;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease;
  }
  
  input:focus,
  textarea:focus {
    outline: none;
    border-color: #007bff; /* Change border color on focus */
  }
  
  button {
    background-color: #007bff;
    color: #fff;
    padding: 15px 25px;
    font-size: 1.2em;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  /* Animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .fade-in {
    animation: fadeIn 1s ease;
  }
  